import Home from './pages/Home';
import Cart from './pages/Cart'; 
import PizzaDetails from './pages/PizzaDetails ';
import { HashRouter, Routes, Route } from 'react-router-dom'; // Utilisation de Navigate pour redirections
import './styles/global.css'


const Router = () => {

  // Dfinir les routes
const routes = [
  { path: "/", element: <Home /> },
  { path: "/home", element: <Home /> },
  { path: "/cart/:category", element: <Cart/> },
  { path: "/cart/:category/:productName", element: <PizzaDetails/> }
];

  return (
    <HashRouter>
      <Routes>
          {/* Routes protges */}
          {routes.map(({ path, element }, index) => (
            <Route
              key={index}
              path={path}
              element={element}
            />
          ))}
        
        {/* Ajouter une route 404 par dfaut si aucune correspondance n'est trouve */}
        <Route path="*" element={<div>Page not found</div>} />
      </Routes>
    </HashRouter>
  );
}

export default Router;
